.slide-left-enter-active {
    transform: translate3d(0, 0, 0);
    transition: transform $_animate_time ease-out;
    z-index: 2;
}

.slide-left-enter {
    transform: translate3d(-100%, 0, 0)
}

.slide-left-leave-active {
    transform: translate3d(-100%, 0, 0);
    transition: transform $_animate_time ease-out;
    z-index: 1;
}

.slide-left-leave {
    transform: translate3d(0, 0, 0)
}