/*控件 样式*/
.cs-toast {
  position: fixed;
  max-width: 80%;
  border-radius: 5px;
  background: rgba(0, 0, 0, 0.7);
  color: #fff;
  box-sizing: border-box;
  text-align: center;
  z-index: 1000;
  transition: opacity .3s linear; }
  .cs-toast .cs-toast-icon {
    display: block;
    text-align: center;
    font-size: 2.48889rem; }
  .cs-toast .cs-toast-text {
    font-size: 1.06667rem;
    display: block;
    text-align: center; }
  .cs-toast.is-placetop {
    top: 50px;
    left: 50%;
    transform: translate(-50%, 0); }
  .cs-toast.is-placemiddle {
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%); }
  .cs-toast.is-placebottom {
    bottom: 50px;
    left: 50%;
    transform: translate(-50%, 0); }
  .cs-toast.toast-pop-enter, .cs-toast.toast-pop-leave-active {
    opacity: 0; }

.screen-page {
  background: #fafdff;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  position: absolute;
  /*滚动条宽度*/
  /* 轨道样式 */
  /* Handle样式 */
  /*当前窗口未激活的情况下*/
  /*hover到滚动条上*/
  /*滚动条按下*/ }
  .screen-page ::-webkit-scrollbar {
    width: 5px;
    border-radius: 2px; }
  .screen-page ::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background: rgba(0, 0, 0, 0.2); }
  .screen-page ::-webkit-scrollbar-thumb:window-inactive {
    background: rgba(0, 0, 0, 0.1); }
  .screen-page ::-webkit-scrollbar-thumb:vertical:hover {
    background-color: rgba(0, 0, 0, 0.3); }
  .screen-page ::-webkit-scrollbar-thumb:vertical:active {
    background-color: rgba(0, 0, 0, 0.7); }

.cs-header {
  height: 3.2rem;
  line-height: 3.2rem;
  text-align: center;
  background: #26a2ff;
  display: flex;
  padding: 0 10px; }
  .cs-header h1 {
    font-size: 1.06667rem;
    color: white;
    flex: 1;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap; }
  .cs-header .cs-header-button {
    flex: .5; }
    .cs-header .cs-header-button.left {
      text-align: left; }
    .cs-header .cs-header-button.right {
      text-align: right; }
    .cs-header .cs-header-button .cs-button {
      background-color: transparent;
      border: 0;
      box-shadow: none;
      color: inherit;
      display: inline-block;
      vertical-align: top;
      padding: 0;
      font-size: inherit;
      color: white;
      height: 3.2rem; }
      .cs-header .cs-header-button .cs-button::after {
        content: none; }

.cs-contenter {
  flex: 1;
  width: 100%;
  z-index: 1;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch; }

.cs-footer {
  height: 3.2rem;
  background: #26a2ff;
  text-align: center; }

.cs-button {
  appearance: none;
  border-radius: 0.21333rem;
  border: 0;
  box-sizing: border-box;
  color: inherit;
  display: block;
  font-size: 0.88889rem;
  height: 3.2rem;
  outline: 0;
  overflow: hidden;
  position: relative;
  text-align: center; }
  .cs-button::after {
    background-color: #000;
    content: " ";
    opacity: 0;
    position: absolute 0 0 0 0; }
  .cs-button.default {
    color: #656b79;
    background-color: #f6f8fa;
    box-shadow: 0 0 1px #b8bbbf; }
  .cs-button.primary {
    color: #fff;
    background-color: #26a2ff; }
  .cs-button.danger {
    color: #fff;
    background-color: #ef4f4f; }
  .cs-button.large {
    display: block;
    width: 100%; }
  .cs-button.normal {
    display: inline-block;
    padding: 0 0.42667rem; }
  .cs-button.small {
    display: inline-block;
    font-size: 0.49778rem;
    padding: 0 0.42667rem;
    height: 2.84444rem; }
  .cs-button.disabled {
    opacity: .6; }

.cs-field {
  box-sizing: border-box;
  width: 100%;
  display: flex;
  align-items: center; }
  .cs-field.cs-field--left input {
    padding-right: 1.24444rem; }
  .cs-field.cs-field--right input {
    padding-left: 1.24444rem; }
  .cs-field i {
    width: 3.55556rem;
    text-align: center;
    line-height: 1; }
  .cs-field input {
    flex: 1;
    background: transparent;
    border: none;
    align-self: stretch; }

/*切换动画*/
.fade-enter-active {
  transition: opacity 0.2s ease-out;
  z-index: 2; }

.fade-enter {
  opacity: 0;
  z-index: 2; }

.fade-leave-active {
  transition: opacity 0.2s ease-in;
  opacity: 0; }

.fade-leave {
  opacity: 1; }

.slide-left-enter-active {
  transform: translate3d(0, 0, 0);
  transition: transform 0.2s ease-out;
  z-index: 2; }

.slide-left-enter {
  transform: translate3d(-100%, 0, 0); }

.slide-left-leave-active {
  transform: translate3d(-100%, 0, 0);
  transition: transform 0.2s ease-out;
  z-index: 1; }

.slide-left-leave {
  transform: translate3d(0, 0, 0); }

.slide-right-enter {
  transform: translate3d(100%, 0, 0); }

.slide-right-enter-active {
  transform: translate3d(0, 0, 0);
  transition: transform 0.2s ease-out;
  z-index: 2; }

.slide-right-leave-active {
  transform: translate3d(100%, 0, 0);
  transition: transform 0.2s ease-out;
  z-index: 1; }

.slide-right-leave {
  transform: translate3d(0, 0, 0); }

.slide-bottom-enter-active {
  transform: translate3d(0, 0, 0);
  transition: transform 0.2s ease-out;
  z-index: 2; }

.slide-bottom-enter {
  transform: translate3d(0, 100%, 0); }

.slide-bottom-leave-active {
  transform: translate3d(0, 100%, 0);
  transition: transform 0.2s ease-out;
  z-index: 1; }

.slide-bottom-leave {
  transform: translate3d(0, 0, 0); }

.slide-top-enter-active {
  transform: translate3d(0, 0, 0);
  transition: transform 0.2s ease-out;
  z-index: 2; }

.slide-top-enter {
  transform: translate3d(0, -100%, 0); }

.slide-top-leave-active {
  transform: translate3d(0, -100%, 0);
  transition: transform 0.2s ease-out;
  z-index: 1; }

.slide-top-leave {
  transform: translate3d(0, 0, 0); }

